import { lazy } from 'react';

const Index = lazy(() => import('../page/sys/index'));

// 移动端
const Ucenter = lazy(() => import('../page/mobile/ucenter/ucenter'));
const Bind = lazy(() => import('../page/mobile/bind/bind'));

const routers = [
    {
        path: '/',
        component: Index,
        name: '医疗事物系统'
    },
    {
        path: '/h5/ucenter',
        component: Ucenter,
        name: '个人中心'
    },
    {
        path: '/h5/bind',
        component: Bind,
        name: '绑定帐号'
    }
];

export default routers;