import {
    CHANGE_BL_DETAIL,
    CHANGE_BL_ID,
    CHANGE_BL_IOT,
    CHANGE_BL_MESSAGE_APP,
    CHANGE_BL_MESSAGE_WC,
    ADD_BL_MESSAGE_APP,
    ADD_BL_MESSAGE_WC,
    DEL_BL_MESSAGE_WC,
} from './action-type';
const tempState = {
    id: '',
    name: '',
    detail: {},
    appMessages: [],
    wcMessages: [],
    iot: [
        {
            name: '蓝牙绑定',
            status: 0,
            last: '',
            times: '',
        },
        {
            name: '血压计',
            status: 0,
            last: '2021-05-14',
            times: 0
        },
        {
            name: '血糖计',
            status: 0,
            last: '2021-06-14',
            times: 1
        },
        {
            name: '体温计',
            status: 0,
            last: '2021-05-28',
            times: 2
        },
    ],
    defaultState: {
        id: '',
        name: '',
        detail: {},
        appMessages: [],
        wcMessages: [],
        iot: [
            {
                name: '蓝牙绑定',
                status: 0,
                last: '',
                times: '',
            },
            {
                name: '血压计',
                status: 0,
                last: '2021-05-14',
                times: 0
            },
            {
                name: '血糖计',
                status: 0,
                last: '2021-06-14',
                times: 1
            },
            {
                name: '体温计',
                status: 0,
                last: '2021-05-28',
                times: 2
            },
        ],
    },
};

export const bingli = (state = tempState, action) => {
    const { type, data } = action;
    switch (type) {
        case CHANGE_BL_ID:
            return {
                ...state,
                ...state.defaultState,
                ...data,
            };
        case CHANGE_BL_DETAIL:
            return {
                ...state,
                detail: data,
            };
        case CHANGE_BL_MESSAGE_APP:
            return {
                ...state,
                appMessages: data,
            };
        case ADD_BL_MESSAGE_APP:
            return {
                ...state,
                appMessages: state.appMessages.concat([data]),
            };
        case CHANGE_BL_MESSAGE_WC:
            return {
                ...state,
                wcMessages: data,
            };
        case ADD_BL_MESSAGE_WC:
            return {
                ...state,
                wcMessages: state.wcMessages.concat([data]),
            };
        case DEL_BL_MESSAGE_WC:
            return {
                ...state,
                wcMessages: state.wcMessages.filter(v => v.time !== data.time).concat([{ role: 'notice-del', time: Date.now(), text: '删除一条消息' }])
            }
        case CHANGE_BL_IOT:
            return {
                ...state,
                iot: data
            };
        default:
            return state;
    }
};